@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@400;500;700&display=swap");
[data-theme=light] {
  --base-color: #24b0c7;
  --secondary-color: #24b0c738;
  --brand-color: var(--base-color);
  --primary-color: var(--base-color);
  --primary: var(--base-color);
  --highlight-color: var(--secondary-color);
}

.btn-primary:not(:disabled):not(.disabled):active,
div#driver-popover-item .driver-popover-footer button.driver-next-btn:not(:disabled):not(.disabled):active,
.btn-primary:not(:disabled):not(.disabled).active,
div#driver-popover-item .driver-popover-footer button.driver-next-btn:not(:disabled):not(.disabled).active,
.show > .btn-primary.dropdown-toggle,
div#driver-popover-item .driver-popover-footer .show > button.dropdown-toggle.driver-next-btn {
  color: #fff;
  background-color: var(--primary);
  border-color: white;
}

body {
  font-family: "Cairo", sans-serif !important;
}

.chart-container {
  font-family: "Cairo", sans-serif !important;
}

.layout-side-section {
  padding-top: 15px;
  color: #fff !important;
}
@media screen and (min-width: 767px) {
  .layout-side-section {
    background-color: #24b0c7;
  }
}
.layout-side-section .icon-btn {
  background-color: transparent !important;
  border: 1px solid white;
}
.layout-side-section .icon,
.layout-side-section .sidebar-label .icon {
  --icon-stroke: #fff;
  stroke: #fff !important;
  fill: transparent;
  stroke-width: 2;
  stroke: #fff;
}
.layout-side-section .sidebar-label {
  color: #fff;
}
.layout-side-section p,
.layout-side-section .text-muted {
  color: #fff !important;
}
.layout-side-section .checkbox .label-area {
  color: #fff;
}
.layout-side-section a.add-group-by,
.layout-side-section a.list-tag-preview {
  color: #fff;
}
.layout-side-section a.add-group-by:hover,
.layout-side-section a.list-tag-preview:hover {
  color: #fff;
}
.layout-side-section .sidebar-item-control .btn {
  background: transparent;
  border: 1px solid white;
}

header.navbar.navbar-expand.sticky-top {
  background-color: #24b0c7;
}

.widget.shortcut-widget-box,
.widget.links-widget-box {
  border-radius: 5px;
}
.widget.shortcut-widget-box .widget-head .widget-label .widget-title,
.widget.links-widget-box .widget-head .widget-label .widget-title {
  color: white !important;
}
.widget.shortcut-widget-box .widget-head .widget-label .widget-title svg,
.widget.links-widget-box .widget-head .widget-label .widget-title svg {
  --icon-stroke: #fff;
  stroke: #fff !important;
  fill: transparent;
  stroke-width: 2;
  stroke: #fff;
}

.widget.edit-mode .widget-head .widget-label .widget-title {
  color: #1f272e;
}

.page-container {
  background-color: rgba(36, 176, 199, 0.2196078431);
}
.page-container .page-head {
  background-color: rgba(36, 176, 199, 0.2196078431);
}

.widget.links-widget-box {
  background: #24b0c7 100% 0% no-repeat padding-box;
  border-radius: 10px;
  padding: 15px;
}
.widget.links-widget-box .link-item {
  color: black;
  background-color: var(--fg-hover-color);
  margin-bottom: 7px;
}
.widget.links-widget-box .link-item:hover .indicator-pill {
  background: #24b0c7;
  color: white;
}

.widget.links-widget-box.skeleton-card {
  background-color: var(--skeleton-bg);
}

.list-sidebar .sidebar-section {
  padding: 10px;
}

.desk-sidebar-item.standard-sidebar-item {
  padding: 7px;
  margin-bottom: 0;
}
.desk-sidebar-item.standard-sidebar-item:hover {
  background-color: #24b0c7;
  border-radius: 0;
}
.desk-sidebar-item.standard-sidebar-item a:hover {
  color: white;
}
.desk-sidebar-item.standard-sidebar-item .sidebar-item-control svg {
  stroke: white;
}

.standard-sidebar .standard-sidebar-item {
  background-color: #57a182;
}
.standard-sidebar .standard-sidebar-item:hover {
  background-color: #24b0c7;
}
.standard-sidebar .standard-sidebar-item.selected {
  background-color: #24b0c7;
  border-radius: 0;
  border-inline-start: 2px solid white;
}

.search-dialog .search-results .search-sidebar .standard-sidebar-item {
  background-color: #57a182;
}
.search-dialog .search-results .search-sidebar .standard-sidebar-item:hover {
  background-color: #24b0c7;
}
.search-dialog .search-results .search-sidebar .standard-sidebar-item.selected {
  background-color: #24b0c7;
}

.desk-sidebar .standard-sidebar-item:hover {
  background-color: #24b0c7;
}
.desk-sidebar .standard-sidebar-item.selected {
  background-color: #24b0c7;
  border-radius: 0;
}

.desk-sidebar-item.selected {
  background-color: #24b0c7;
  padding-inline-start: 4px;
  border-inline-start: 4px solid white;
}
.desk-sidebar-item.selected a {
  color: white;
}

.page-content .desk-sidebar-item a {
  color: white;
}

#navbar-breadcrumbs {
  display: none !important;
}
#navbar-breadcrumbs li.disabled a {
  color: #fff;
}
#navbar-breadcrumbs a {
  color: white;
}
#navbar-breadcrumbs a::before {
  content: " ";
  border: solid #fff;
  border-width: 0 0 2px 2px;
  display: inline-block;
  padding: 3px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  margin-left: 10px;
}

[data-page-route=Workspaces] .layout-main-section {
  border-radius: 0;
}
[data-page-route=Workspaces] .sidebar-item-container .sidebar-item-container {
  margin-right: 0;
}
[data-page-route=Workspaces] .sidebar-item-container .sidebar-item-container .desk-sidebar-item {
  padding-inline-start: 20px;
}
[data-page-route=Workspaces] .sidebar-item-container .sidebar-item-container .desk-sidebar-item.selected {
  padding-inline-start: 16px;
}
[data-page-route=Workspaces] .btn-reset.standard-sidebar-label {
  display: none;
}
[data-page-route=Workspaces] .layout-main .layout-side-section {
  scrollbar-color: #24b0c7 transparent;
  scrollbar-width: unset;
  padding: 0;
  padding-top: 10px;
  padding-inline-start: 10px;
  width: auto;
}
[data-page-route=Workspaces] .layout-main .layout-side-section::-webkit-scrollbar {
  width: 5px;
  scrollbar-color: #24b0c7 transparent;
  scrollbar-width: unset;
}
[data-page-route=Workspaces] .layout-main .layout-side-section::-webkit-scrollbar-thumb {
  background: #24b0c7;
  border-radius: 20px;
  scrollbar-color: #24b0c7 transparent;
  scrollbar-width: unset;
}
[data-page-route=Workspaces] .layout-main .layout-side-section::-webkit-scrollbar-track {
  background: #ddd;
  border-radius: 20px;
}
[data-page-route=Workspaces] .layout-main-section-wrapper {
  scrollbar-color: #24b0c7 transparent;
  scrollbar-width: unset;
}
[data-page-route=Workspaces] .layout-main-section-wrapper::-webkit-scrollbar {
  width: 5px;
  scrollbar-color: #24b0c7 transparent;
  scrollbar-width: unset;
}
[data-page-route=Workspaces] .layout-main-section-wrapper::-webkit-scrollbar-thumb {
  background: #24b0c7;
  border-radius: 20px;
  scrollbar-color: #24b0c7 transparent;
  scrollbar-width: unset;
}
[data-page-route=Workspaces] .layout-main-section-wrapper::-webkit-scrollbar-track {
  background: #ddd;
  border-radius: 20px;
}
[data-page-route=Workspaces] .standard-sidebar-item:focus-within {
  background-color: #24b0c7;
}

.navbar-nav .icon {
  --icon-stroke: #fff;
  stroke: #fff !important;
  fill: transparent;
  stroke-width: 2;
  stroke: #fff;
}

.standard-sidebar-label {
  color: white;
}

.navbar .navbar-nav .nav-link {
  padding: 0;
  color: white;
}

.widget.shortcut-widget-box.skeleton-card {
  background-color: var(--skeleton-bg);
}

.widget.shortcut-widget-box {
  background: #24b0c7 100% 0% no-repeat padding-box;
  border-radius: 30px;
  padding: 15px;
}
.widget.shortcut-widget-box .widget-control > * {
  stroke: white;
}
.widget.shortcut-widget-box .widget-head .widget-title {
  color: #fff !important;
}
.widget.shortcut-widget-box:hover {
  background: #24b0c7 100% 0% no-repeat padding-box;
}
.widget.shortcut-widget-box:hover .widget-title {
  color: #fff !important;
}